/* 房贷计算器 */
<template>
  <div>
    <top :title="title"></top>

    <van-tabs v-model="active">
      <van-field
        placeholder="0"
        class="allprice"
        v-model="allPrice"
        type="number"
        label="房价总额"
        input-align="right"
        right-icon="1"
      ></van-field>
      <span class="wan">万</span>

      <van-cell @click="showProportion = true" is-link label="贷款比例" title>
        <span>{{proportionValue}}</span>
      </van-cell>

      <van-cell label="贷款总额" title>
        <span class="position">{{loanAllPrice}}</span>
        <span class="position">万</span>
      </van-cell>

      <van-cell @click="showYears = true" is-link label="贷款年限" title>
        <span>{{yearsValue}}</span>
      </van-cell>

      <!-- 公积金贷款 -->
      <van-tab class="tab" title="公积金贷款">
        <van-cell @click="OnGongJiJin" is-link label="公积金利率" title>
          <span>{{interestValue}}</span>
        </van-cell>
      </van-tab>

      <!-- 商业贷款 -->
      <van-tab title="商业贷款">
        <van-cell @click="showWay = true" is-link label="利率方式" title>
          <span>{{WayValue}}</span>
        </van-cell>

        <van-field
          v-if="WayValueIndex==1"
          placeholder="0"
          class="allprice jidian"
          v-model="Basepoint"
          type="number"
          label="基点"
          input-align="right"
          right-icon="1"
        ></van-field>
        <span class="basepoint">BP(‱)</span>

        <van-cell v-if="WayValueIndex==1" label="商贷利率" title icon-right="1">
          <span class="position">{{sdlilv}}</span>
        </van-cell>

        <van-cell is-link @click="OnSYD" v-if="WayValueIndex==0" label="商贷利率" title icon-right="1">
          <span class="position">{{Jsdlilv}}</span>
        </van-cell>
      </van-tab>
      <!-- 组合贷款 -->
      <van-tab title="组合贷款">
        <van-field
          placeholder="0"
          class="allprice"
          v-model="zhGJJ"
          type="number"
          label="公积金贷款"
          input-align="right"
          right-icon="1"
        ></van-field>
        <span class="zhGJJ">万</span>

        <!-- 组合贷款页面公积金利率 -->
        <van-cell @click="OnGongJiJin" is-link label="公积金利率" title>
          <span>{{interestValue}}</span>
        </van-cell>

        <!-- 组合贷款页面商业贷款 -->
        <van-field
          placeholder="0"
          class="allprice"
          v-model="sydk"
          type="number"
          label="商业贷款"
          input-align="right"
          right-icon="1"
        ></van-field>
        <span class="sydk">万</span>
        <van-cell @click="showWay = true" is-link label="利率方式" title>
          <span>{{WayValue}}</span>
        </van-cell>

        <van-field
          v-if="WayValueIndex==1"
          placeholder="0"
          class="allprice jidian"
          v-model="Basepoint"
          type="number"
          label="基点"
          input-align="right"
          right-icon="1"
        ></van-field>
        <span class="zhbasepoint">BP(‱)</span>

        <van-cell v-if="WayValueIndex==1" label="商贷利率" title icon-right="1">
          <span class="position">{{sdlilv}}</span>
        </van-cell>

        <van-cell is-link @click="OnSYD" v-if="WayValueIndex==0" label="商贷利率" title icon-right="1">
          <span class="position">{{Jsdlilv}}</span>
        </van-cell>
      </van-tab>

      <van-cell>
        <van-button class="jisuan">开始计算</van-button>
        <p class="cankao">此结果仅供参考，请以当地实际情况为准</p>
      </van-cell>

      <!-- 贷款比例 -->
      <van-popup v-model="showProportion" round position="bottom">
        <van-picker
          show-toolbar
          :columns="proportionColumns"
          @cancel="onProportionCancel"
          @confirm="onProportionConfirm"
        />
      </van-popup>

      <!-- 贷款年限 -->
      <van-popup v-model="showYears" round position="bottom">
        <van-picker
          show-toolbar
          :columns="yearsColumns"
          @cancel="onYearsCancel"
          @confirm="onYearsConfirm"
        />
      </van-popup>

      <!-- 公积金利率 -->
      <van-popup v-model="providentFundShow" round position="bottom">
        <van-cell>
          <span class="lilvname">{{interestRate}}</span>
        </van-cell>
        <!-- 步进器 -->
        <van-slider
          class="slider"
          @change="onSliderChange"
          v-model="SliderValue"
          :step="1"
          :min="7"
          :max="13"
          active-color="#FD5C1F"
        >
          <template #button>
            <div class="button"></div>
          </template>
        </van-slider>
        <div class="lilvbottom">
          <p>7折</p>
          <p>8折</p>
          <p>9折</p>
          <p></p>
          <p>1.1倍</p>
          <p>1.2倍</p>
          <p>1.3倍</p>
        </div>

        <van-field
          input-align="right"
          type="number"
          v-model="gongjijin"
          :label="dkName[index]"
          placeholder="0"
          class="baifenhaoinput"
        ></van-field>
        <span class="baifenhao">%</span>

        <van-cell>
          <van-button @click="onCancel" class="lilvbutton cancelbutton" color="#F8F8F8">取消</van-button>
          <van-button @click="onConfirm" class="lilvbutton" color="#FF561C">确定</van-button>
        </van-cell>
      </van-popup>
      <!-- 利率方式遮罩层 -->
      <van-popup v-model="showWay" round position="bottom">
        <van-picker show-toolbar :columns="lilvway" @cancel="onWayCancel" @confirm="onWayConfirm" />
      </van-popup>
    </van-tabs>
  </div>
</template>
<script>
import Top from "../../components/common/top";
export default {
  name: "Calculator",
  components: { Top },
  data() {
    return {
      title:"房贷计算器",
      /* 默认标签页码 */
      active: 0,
      /* 房价总额 */
      allPrice: "0",
      /* 贷款总额 */
      loanAllPrice: "0",
      /* 贷款比例遮罩层的显示 */
      showProportion: false,
      /* 贷款比例的绑定值 */
      proportionValue: "0%",
      /* 年限遮罩层的绑定值 */
      showYears: false,
      /* 贷款年限的绑定值 */
      yearsValue: "0年",
      /* 公积金显示遮罩层的显示 */
      providentFundShow: false,
      /* 利率步进器的值 */
      SliderValue: 10,
      /* 利率折扣 */
      interestRate: "基准利率",
      /* 利率显示 */
      interestValue: "基准利率(3.25%)",
      /* 输入的公积金利率 */
      gongjijin: "",
      /* 显示利率方式遮罩层 */
      showWay: false,
      /* 利率方式的值 */
      WayValue: "按LPR(4.65%)",
      WayValueIndex: 1,
      /* 基点的值 */
      Basepoint: "0",
      /* 商贷利率 */
      sdlilv: "123456",
      /* 旧版商贷利率 */
      Jsdlilv: "基准利率(4.90%)",
      /* 贷款名字 */
      dkName: ["公积金利率", "商业贷款利率"],
      index: 0,

      /* 组合贷款-公积金 */
      zhGJJ: "0",
      /* 组合贷款-商业贷款 */
      sydk: "0",
      /* 贷款比例 */
      proportionColumns: [
        "20%",
        "25%",
        "30%",
        "35%",
        "40%",
        "45%",
        "50%",
        "55%",
        "60%",
        "65%",
        "70%",
        "75%",
        "80%"
      ],
      /* 贷款年限 */
      yearsColumns: [
        "1年（12期）",
        "2年（24期）",
        "3年（36期）",
        "4年（48期）",
        "5年（60期）",
        "6年（72期）",
        "7年（84期）",
        "8年（96期）",
        "9年（108期）",
        "10年（120期）",
        "11年（132期）",
        "12年（144期）",
        "13年（156期）",
        "14年（168期）",
        "15年（180期）",
        "16年（192期）",
        "17年（204期）",
        "18年（216期）",
        "19年（228期）",
        "20年（240期）",
        "21年（252期）",
        "22年（264期）",
        "23年（276期）",
        "24年（288期）",
        "25年（300期）",
        "26年（312期）",
        "27年（324期）",
        "28年（336期）",
        "29年（348期）",
        "30年（360期）"
      ],
      lilvway: ["按旧版基准利率", "按LPR(4.65%)"]
    };
  },
  methods: {
    /* 选择器的确认事件 */
    onProportionConfirm(value) {
      console.log("贷款比例" + value);
      this.proportionValue = value;
      this.showProportion = false;
    },
    /* 选择器的取消事件 */
    onProportionCancel(value) {
      console.log(value);
      this.showProportion = false;
    },
    /* 贷款年限选择器的确认按钮 */
    onYearsConfirm(value) {
      console.log("贷款年限" + value);
      this.yearsValue = value;
      this.showYears = false;
    },
    /* 贷款年限选择器的取消事件 */
    onYearsCancel(value) {
      console.log(value);
      this.showYears = false;
    },
    onSliderChange(value) {
      console.log(value);
      if (value === 7) {
        this.interestRate = "7折";
      }
      if (value === 8) {
        this.interestRate = "8折";
      }
      if (value === 9) {
        this.interestRate = "9折";
      }
      if (value === 10) {
        this.interestRate = "基准利率";
      }
      if (value === 11) {
        this.interestRate = "1.1倍";
      }
      if (value === 12) {
        this.interestRate = "1.2倍";
      }
      if (value === 13) {
        this.interestRate = "1.3倍";
      }
    },
    /* 公积金利率的点击 */
    OnGongJiJin() {
      this.index = 0;
      this.providentFundShow = true;
    },
    /* 商业贷款利率的点击 */
    OnSYD() {
      this.index = 1;
      console.log(this.index);
      this.providentFundShow = true;
    },
    /* 利率取消按钮 */
    onCancel(a) {
      console.log(a);
      this.providentFundShow = false;
    },

    /* 利率确认按钮 */
    onConfirm(a) {
      console.log(a);
      this.providentFundShow = false;
    },
    onWayCancel(a, b) {
      console.log(a, b);
      this.showWay = false;
    },
    onWayConfirm(a, b) {
      console.log(a, b);
      this.WayValue = a;
      this.showWay = false;
      this.WayValueIndex = b;
    }
  }
  // watch: {
  //   providentFundShow(a) {
  //     console.log(a);
  //     this.index = 0;
  //     if (a === false) {
  //       this.index = 1;
  //     }
  //   }
  // }
};
</script>
<style lang="scss" scoped>
.allprice {
  color: #969799;
  font-size: 1.2rem;
}
.wan {
  position: absolute;
  right: 34px;
  top: 56px;
  font-size: 14px;
  // color: #969799;
}
.position {
  position: relative;
  right: 16px;
}
.button {
  width: 20px;
  height: 20px;
  background-color: #fd5c1f;
  border-radius: 50%;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
}
.van-cell__value--alone {
  text-align: center;
}
.lilvname {
  color: #fd5c1f;
  border: 1px solid#F8F8F8;
  padding: 1px 3px;
  box-shadow: 0 0 3px#E9ECF3;
}
.slider {
  margin: 0 auto;
  margin-top: 25px;
  width: 80%;
}
.lilvbottom {
  width: 85%;
  margin: 0 auto;
  margin-top: 15px;
  font-size: 12px;
  display: flex;
  justify-content: space-between;
  p {
    width: 29px;
  }
}
.baifenhaoinput {
  padding-right: 41%;
}
.baifenhao {
  position: absolute;
  // top: 127px;
  top: 50.5%;
  right: 42%;
  color: #969799;
}
.lilvbutton {
  width: 40%;
  border-radius: 5px;
  height: 35px;
  margin: 20px 10px;
  box-shadow: 0 0 0;
}
.cancelbutton {
  color: #999999 !important;
}
.jisuan {
  width: 100%;
  border-radius: 5px;
  color: #ffffff;
  background: #0087ee;
  margin-top: 10px;
}
.cankao {
  color: #969799;
  font-size: 12px;
}
.basepoint {
  position: absolute;
  right: 35px;
  top: 276px;
  font-size: 14px;
}
.jidian {
  padding-right: 50px;
}
.zhGJJ {
  position: absolute;
  right: 34px;
  top: 232px;
  font-size: 14px;
}
.sydk {
  position: absolute;
  right: 34px;
  top: 58.5%;
  font-size: 14px;
}
.zhbasepoint {
  position: absolute;
  right: 35px;
  top: 408px;
  font-size: 14px;
}
</style>
